import React from 'react';
import { pageAdAtom } from 'Global/shared/adv.page.atom';
import { useAtom } from 'jotai';
import { Container, Stack, Card } from '@mui/material';
import { ArabicToPersian } from 'utils/ConvertToPersianTextFromArabic';

const Advert = () => {
  const advData = JSON.parse(
    sessionStorage.getItem('advImages')
  );

  const title = advData[0]?.Header;
  const imgBase64 = advData[0]?.imageList[0]?.Base64Image;
  const description_text = advData[0]?.Description;
  const external_link = advData[0]?.linkAddress;

  return (
    <Container sx={{ mt: '1em' }}>
      <Stack
        gap=".5em"
        onClick={() => {
          window.open(external_link, '_blank');
        }}
      >
        <h3 style={{ marginTop: '0' }}>
          {ArabicToPersian(title)}
        </h3>
        <img
          src={imgBase64}
          alt={title}
          style={{
            width: 'auto',
            height: '13.75em',
            objectFit: 'cover',
            padding: 0,
            margin: 0,
            borderRadius: '10px',
          }}
        />
        <Card elevation={2} sx={{ p: '.3em', mb: '1em' }}>
          <p>{ArabicToPersian(description_text)}</p>
        </Card>
      </Stack>
    </Container>
  );
};

export default Advert;
